.sidebar {
  background: #f4f6f8;
  height: 100%;
  width: 210px;
  border-right: 1px solid #f1f1f1;
  padding: 40px 20px;
}
.lists {
  display: flex;
  flex-direction: column;
  gap: 3px;
  margin-bottom: 20px;
  max-height: 260px;
  overflow-y: auto;

  scrollbar-width: 2px;
  scrollbar-color: gray;

  &::-webkit-scrollbar {
    height: 112px;
    width: 2px;
  }
  &::-webkit-scrollbar-track {
    background: #f4f6f8;
  }
  &::-webkit-scrollbar-thumb {
    background-color: rgba(105, 105, 105, 0.332);;
    border-radius: 1px;
  }
}
