.addButton {
  cursor: pointer;
  display: flex;
  align-items: center;
  transition-duration: 0.2;
  span {
    font-size: 16px;
    font-weight: 500;
    color: #b4b4b4;
    margin-left: 10px;
  }
  &:hover {
    opacity: 0.8;
  }
}
.icon {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 20px;
  height: 20px;
  opacity: 0.2;
}

