.addButton {
  display: flex;
  align-items: center;
  padding: 10px 10px;
  color: #767676;
  transition-duration: 0.3s;
  cursor: pointer;
  img {
    width: 10px;
    height: 10px;
    opacity: 0.5;
    margin: 2px 10px 0px 4px;
  }
  &:hover {
    opacity: 0.5;
  }
  &:active {
    opacity: 0.3;
  }
}
.popUp {
  padding: 15px;
  background: white;
  width: 235px;
  height: 148px;
  box-shadow: 0px 6px 10px 0px #0000000d;
  border-radius: 10px;
  display: flex;
  flex-direction: column;
  position: relative;
}
.colorList {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-grow: 1;
}
.badge {
  width: 20px;
  height: 20px;
  border-radius: 50%;
  cursor: pointer;
}
.selected {
  border: 2px solid #525252;
}

.closeButton {
  cursor: pointer;
  width: 20px;
  height: 20px;
  position: absolute;
  right: -8px;
  top: -8px;
  transition-duration: 0.2s;
  &:hover {
    opacity: 0.9;
  }
  &:active {
    opacity: 0.8;
  }
}
