.tasks {
  flex-grow: 1;
  padding: 50px;
}
.tasksTable {
  max-height: 250px;
  overflow-y: auto;
  display: flex;
  flex-direction: column;
  gap: 20px;
  margin-bottom: 20px;
  padding: 30px 0px;

  border-bottom: 1px solid #f2f2f2;
  border-top: 1px solid #f2f2f2;

  scrollbar-width: 2px;
  scrollbar-color: gray;

  &::-webkit-scrollbar {
    height: 112px;
    width: 2px;
  }
  &::-webkit-scrollbar-track {
    background: #f4f6f8;
  }
  &::-webkit-scrollbar-thumb {
    background-color: rgba(105, 105, 105, 0.332);
    border-radius: 1px;
  }
}
