.task {
  display: flex;
  align-items: center;
  padding-right: 10px;
}

.checkbox {
  input {
    display: none;
  }

  label {
    transition-duration: 0.2s;
    cursor: pointer;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 20px;
    height: 20px;
    border: 2px solid #e8e8e8;
    border-radius: 50%;

    svg {
      transition-duration: 0.2s;
      opacity: 0;
    }

    &:hover {
      border-color: #f2f2f2;
      background: #f2f2f2;
      svg {
        opacity: 1;
        path {
          stroke: #b3b3b3;
        }
      }
    }
  }

  input:checked + label {
    border-color: #4dd599;
    background: #4dd599;
    svg {
      opacity: 1;
      path {
        stroke: white;
      }
    }
  }
}

.taskText {
  display: flex;
  align-items: center;
  margin-left: 10px;
  font-size: 14px;
  font-weight: 500;
  width: 100%;
  justify-content: space-between;
  &:hover {
    .removeIcon {
      display: inline-block;
    }
  }
}
.removeIcon {
  cursor: pointer;
  width: 10px;
  height: 10px;
  display: block;
  display: none;
  opacity: 0.2;
  transition-duration: 0.2s;
  &:hover {
    opacity: 0.4;
  }
}
