.listItem {
  display: flex;
  align-items: center;
  padding: 10px 10px;
  border-radius: 4px;
  cursor: pointer;
  transition: 0.15s;
  &:hover {
    background-color: rgba(255, 255, 255, 0.608);
    box-shadow: 0px 2px 10px 0px #00000008;
    .removeButton {
      display: inline-block;
    }
  }
}
.title {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  flex: 1 1 auto;
  margin-right: 2px;
  &:first-letter {
    text-transform: uppercase;
  }
}
.icon {
  min-width: 18px;
  height: 18px;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-right: 6px;
}

.badge {
  width: 10px;
  height: 10px;
  background: #000;
  border-radius: 50%;
}

.removeButton {
  display: none;
  opacity: 0.2;
  transition: 0.15s;
  cursor: pointer;
  &:hover {
    opacity: 0.5;
  }
}
.active {
  background: white;
  box-shadow: 0px 2px 10px 0px #00000008;
}
