.button {
  cursor: pointer;
  display: inline-block;
  padding: 10px 20px;
  text-align: center;
  border-radius: 4px;
  transition-duration: 0.2s;
  color: white;
  &:hover {
    opacity: 0.8;
  }
  &:active {
    opacity: 0.5;
  }
}
.green {
  background: #4dd599;
  color: white;
}
.gray {
  background: #f4f6f8;
  color: #9c9c9c;
}
